import React from 'react';
import {Link} from 'react-router-dom';

class HomePage extends React.Component{
    constructor(props){
        super(props)
        this.state={
        }
    }

    render(){
        return(
         <div style={{display:"flex",justifyContent:"center"}}>
               <div className="text-center" style={{fontSize:"18px",fontFamily:"sans-serif",marginTop:"5%"}}>
                <h4 style={{color:"#666669"}}></h4>
               <div><Link className="btn btn-sm btn-outline-dark" to="/adduser" style={{textDecoration:"none",marginTop:"10px",width:"7rem"}}></Link></div>
               <div><Link className="btn btn-sm btn-outline-dark" to="/addorganization" style={{textDecoration:"none",marginTop:"10px",width:"7rem"}}></Link></div>
               <div><Link className="btn btn-sm btn-outline-dark" to="/userrolelist" data-toggle='tooltip' title="" style={{textDecoration:"none",marginTop:"10px",width:"7rem"}}></Link></div>
               <div><Link className="btn btn-sm btn-outline-dark" to="/roleauthforuser" style={{textDecoration:"none",marginTop:"10px",width:"7rem"}}></Link></div>
               </div>
               <div className="text-center" style={{fontSize:"18px",fontFamily:"sans-serif",marginLeft:"20%",marginTop:"5%"}}>
                <h4 style={{color:"#666669"}}></h4>
                <div><Link className="btn btn-sm btn-outline-dark" to="/addrole" style={{textDecoration:"none",marginTop:"10px",width:"7rem"}}></Link></div>
                <div><Link className="btn btn-sm btn-outline-dark" data-toggle='tooltip' title="" to="/userlist" style={{textDecoration:"none",marginTop:"10px",width:"7rem"}}></Link></div>
               </div>
         </div>
        )
    }
}

export default HomePage